@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700);
/* ================================= 
  WEB FONTS
==================================== */
body {
  color: #576366;
  background: #4FB848;
  margin: 0;
  font-family: 'Roboto', sans-serif; }

ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

a {
  text-decoration: none;
  color: #4FB848; }

hr {
  border: 0;
  height: 0;
  margin: 2.3em 0 2em 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1); }

.container {
  width: 95%;
  max-width: 780px;
  padding-bottom: 2em;
  margin: 2em auto;
  background: #fff;
  border-radius: .5em;
  box-shadow: 0 3px rgba(0, 0, 0, 0.1);
  overflow: hidden; }

header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 1em 2em;
  color: #fff;
  background: #ABD9EC;
  margin-bottom: 2em; }

/* Main Nav -------- */
.main-nav {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-self: center;
          align-self: center;
  -webkit-align-items: baseline;
          align-items: baseline; }
  .main-nav a {
    color: #fff;
    font-size: 1.20em;
    text-align: center;
    text-transform: uppercase;
    -webkit-transition: .2s;
    transition: .2s;
    padding-left: 10px; }
  .main-nav li {
    display: inline-block;
    vertical-align: middle; }

.main-nav a:hover,
.main-nav .active {
  font-size: 1.5em; }

/* FORM */
form input {
  width: 200px;
  outline: none;
  font-size: 20;
  padding: 10px;
  border: none;
  border-radius: 4px;
  background-color: rgba(221, 221, 221, 0.3);
  margin-top: 10;
  margin-right: 5px; }

form .button {
  width: 180;
  height: 50;
  border: none;
  border-radius: 4px;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: .25s all;
  transition: .25s all;
  color: white; }

form .button:enabled {
  background-color: #ABD9EC;
  width: 220; }

/* SUBJECTS */
.subjects {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin-top: 10px; }

.subject {
  border: none;
  background-color: #4FB848;
  border-radius: 4px;
  color: white;
  margin-right: 5px;
  cursor: pointer; }

.subject:hover {
  background-color: #FF4528; }

/* COURSE FINDER */
.course-finder {
  padding: 1em 2em;
  width: 90%; }

.courses {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between; }

.course {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  width: 40%;
  border: 1px solid pink;
  border-radius: 4px;
  padding: 10px;
  margin-bottom: 10px;
  margin-right: 10px; }
  .course p {
    font-size: 0.8em; }

.game-container {
  min-height: 200px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly; }
  .game-container img {
    -webkit-align-self: center;
            align-self: center; }

.imgcoin {
  cursor: pointer; }

@-webkit-keyframes jump {
  from {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg); }
  to {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg); } }

@keyframes jump {
  from {
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg); }
  to {
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg); } }

.boy {
  -webkit-animation: jump 5s steps(50, end) infinite;
          animation: jump 5s steps(50, end) infinite; }

.boy:hover {
  -webkit-animation: none;
          animation: none; }

.game-index {
  text-align: center; }

